<template>

  <tr style="cursor: pointer" v-on:click="moveDetail(`/board/read?boardId=${boardId}`)">
    <td>{{ boardId }}</td>
    <td>
      {{ title }}
    </td>
    <td>{{ userSeq }}</td>
    <td>{{ getFormatDate(regDt) }}</td>
  </tr>

</template>

<script>
import moment from 'moment';

export default {
  name: 'row',
  props: {
    boardId: { type: Number },
    userSeq: { type: String },
    title: { type: String },
    regDt: { type: String },
    uid: { type : String },
  },
  methods: {
    getFormatDate(regDt) {
      console.log(this.uid);
      return moment(new Date(regDt)).format('YYYY.MM.DD');
    },
    moveDetail(url){
      this.$router.push(url);
    }
  },
};
</script>
