<template>
  <div>
    <div v-if="items.length" class="table-wrapper">
      <table >
        <thead>
        <tr>
          <th>번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>날짜</th>
        </tr>
        </thead>
        <tbody>
        <list-row
          v-for="(item, index) in items"
          :key="`${index}_items`"
          :boardId="item.boardId"
          :title="item.title"
          :userSeq="item.userSeq"
          :regDt="item.regDt"
        />
        </tbody>
      </table>
    </div>
    <div v-else>글이 없습니다.</div>

    <div class="btn-cover">
      <ul class="pagination justify-content-center">
        <li class="page-item" v-for="(page, idx) in pageCount" :key="idx" @click="movePaging(page)">{{ page }}</li>
      </ul>
    </div>

    <div class="text-right" v-if="isLogin && uid=='admin'">
      <button class="btn special" @click="movePage">등록</button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
import ListRow from '@/components/Row.vue';
import store from "@/store/store.js";

export default {
  name: 'list',
  components: {
    ListRow,
  },
  data: function() {
    return {
      items: [],
      isLogin: false,
      uid: '',
      pageNum: 1,
      pageSize: 10,
      totalCnt: 0,
    };
  },
  mounted() {
    this.isLogin = store.state.isLogin;
    this.uid = store.state.uid;
    console.log(this.uid);
    http
      .get('/board/total')
      .then(({ data }) => {
        console.log(data);
        this.totalCnt = data.totalCnt;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });

    console.log("created");
    http
      .post('/board/page', {
          offset: (0)*10,
          limit: 10,
      })
      .then(({ data }) => {
        console.log(data);
        this.items = data;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
  },
  methods: {
    movePage() {
      this.$router.push('/board/create');
    },
    movePaging(page){
      this.pageNum = page;
      http
      .post('/board/page', {
          offset: (this.pageNum-1)*this.pageSize,
          limit: this.pageSize,
      })
      .then(({ data }) => {
        console.log(data);
        this.items = data;
      })
      .catch(() => {
        alert('에러가 발생했습니다.');
      });
    },
  },
  computed: {
    pageCount() {
      let page = Math.floor((this.totalCnt-1) / this.pageSize)+1;
      return page;
    },
  }
};
</script>

